:root {
  --sidenav-width: 300px;

  --pal-cream: #FEF9EF;
  --pal-white: #FFFFFF;
  --pal-blue: #227C9D;
  --pal-green: #17C3B2;
  --pal-red: #FE6D73;
  --pal-yellow: #FFCB77;
  --pal-violet: #905ebf;

  --pal-main: var(--pal-green);
  --pal-secondary: var(--pal-blue);
  --pal-text: var(--pal-cream);

  --neutral-shadow: drop-shadow(3px 3px 2px rgba(40, 44, 52, 0.2));
}

body > #root > div {
  height: 100vh;
}

.App {
  margin-left: var(--sidenav-width);

  background-color: var(--pal-cream);
  color: var(--pal-text);

  z-index: 2;
}

.App-nobar {
  background-color: var(--pal-cream);
  overflow-x: hidden;
  padding: 2%;

  box-sizing: border-box;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.app-shadow {
  filter: drop-shadow(0px 0px 2px rgba(40, 44, 52, 0.5));
  z-index: 0;

  background-color: var(--pal-cream);

  padding: 10px;

  top: 0;
  left: var(--sidenav-width);

  overflow-x: hidden;

  min-height: 100%;
}

.definable-inactive {
  display: inline;
}

.definable {
  display: inline;
  color: var(--pal-secondary);
  font-weight: bold;

  visibility: visible;
}

.definable-hovered {
  display: inline;
  color: var(--pal-main);
  font-weight: bold;
  text-decoration-line: underline;

  visibility: visible;
}

.definition {
  visibility: hidden;
  font-weight: normal;

  position: fixed;
  
  width: 50px;
  height: 50px;

  z-index: 6;
}

.definition-hovered {
  visibility: visible;
  font-weight: normal;
  filter: drop-shadow(4px 4px 0px var(--pal-secondary));

  position: fixed;
  width: 50px;
  height: 50px;

  top: 0;
  left: 0;

  z-index: 3;
}

.def-button {
  color: var(--pal-cream);
  background-color: var(--pal-secondary);
  border-radius: 8px;
  position: absolute;
  right: 8px;
  bottom: 8px;

  padding: 10px;
}

.def-box {
  position: absolute;
  width: 1000px;
  height: 500px;
  left: calc(var(--sidenav-width) + 4vh);
  top: calc(83px + 4vh);
}

.def-blurb {
  height: 100%;
  text-align: left;
  padding: 8px;

  width: 100%;
  color: var(--pal-cream);
  background-color: var(--pal-main);
  box-sizing: border-box;
}

.def-styled {
  height: 150px;
  fill: var(--pal-main);
}

.titlefloat {
  display: flex;
  justify-content: flex-end;

  font-size: 80pt;
  width: 100%;
  text-align: right;
  padding-top: 20px;
  padding-right: 10px;

  color: var(--pal-main);

  font-family: 'Lexend', sans-serif;
  text-shadow: 4px 4px 0px var(--pal-secondary);
}

.titlefloat-box {
  width: 50%;
}

.titleshape {
  position: fixed;
  top: -95px;
  left: -5px;
  z-index: 2;

  filter: drop-shadow(4px 4px 0px var(--pal-secondary));

  width: 100%;
  pointer-events: none;
}

.titleshape-styled {
  height: auto;
  fill: var(--pal-main);
  transform: translate(0px, 84px);
}

.titleshape-box {
  position: absolute;
  width: 100%;
}

.titleshape-blurb {
  font-size: 36pt;
  height: 100%;
  text-align: left;
  padding: 4px;
  padding-top: 12px;
  padding-left: 20px;

  width: fit-content;
  color: var(--pal-cream);
  background-color: var(--pal-main);
  box-sizing: border-box;

  font-family: 'Lexend', sans-serif;
  text-shadow: 4px 4px 0px var(--pal-secondary);

  pointer-events: auto;

  display: inline-block;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.mentor {
  position: fixed; /* Fixed Sidebar (stay in place on scroll) */
  top: calc(100vh - 10vw);
  left: calc(100vw - 10vw - 18px);
  width: 8vw;
  height: 8vw;
  background-color: var(--pal-main); /* Black */

  z-index: 6;
}

.left-scroll-outer {
  direction: rtl;
  width: 100%;

  overflow-y: scroll;
  flex: 1 1 auto;
}

.left-scroll-inner {
  direction: ltr;
}

.left-scroll-inner::before {
  content: '';
  position: absolute;
  width: 300px;
  height: 25px;
  background: linear-gradient(var(--pal-main), rgba(255, 255, 255, 0.001));
  z-index: 2;
}

.left-scroll-inner::after {
  content: '';
  position: absolute;
  bottom: 0;
  width: 300px;
  height: 25px;
  background: linear-gradient(rgba(255, 255, 255, 0.001), var(--pal-main));
  z-index: 2;
}

.App-sidenav a {
  color: var(--pal-text);
  text-decoration-line: none;
}

.sidenav-title {
  font-size: xx-large;
  font-family: 'Lexend', sans-serif;

  text-align: center;
  padding-top: 10px;

  flex: 0 1 auto;
}

.sidenav-acc {
  padding: 0;
}

.sidenav-top-acc {
  padding-right: 5px;
  padding-bottom: 5px;
}

.sidenav-acchead {
  margin-bottom: 5%;
  font-size: small;
  
  display: flex;
  flex-direction: row;
  align-items: center;
}

.sidenav-accbody {
  padding: 0%;
  padding-left: 10%;
}

.sidenav-accitem {
  border-width: 0px;
  background-color: transparent;
}

.sidenav-accdropdown {
  padding: 2%;
  display: inline-flex;

  height: 25px;
  width: 27.2px;

  background-color: transparent;
  border-color: transparent;
  border-radius: 10%;
  box-shadow: none;
}

.sidenav-accdropdown.accordion-button:not(.collapsed) {
  background-color: transparent;
  border-color: transparent;
  border-radius: 10%;
  box-shadow: none;
}

.sidenav-accdropdown.accordion-button:focus {
  border-color: transparent;
  box-shadow: none;
}

.sidenav-accbutton {
  padding: 2%;
  background-color: transparent;
  width: 80%;
  float: right;
}

.search {
  background: rgba(255, 255, 255, 0.34);
  border-radius: 4px;
  font-size: medium;
  padding: 10px;
  margin: 8px;

  filter: var(--neutral-shadow);
}

