.App-sidenav {
  display: flex;
  flex-flow: column;
  height: 100%;

  width: var(--sidenav-width); /* Set the width of the sidebar */
  position: fixed; /* Fixed Sidebar (stay in place on scroll) */
  z-index: 0; /* Stay on top */
  top: 0; /* Stay at the top */
  left: 0;
  background-color: var(--pal-main); /* Black */
  overflow-x: hidden; /* Disable horizontal scroll */
  padding: 5px;
}

.sidenav-icon {
  height: 100%;
  width: 100%;
}

.sidenav-icon.shadow-blue {
  filter: drop-shadow(4px 4px 0px var(--pal-blue));
}

.sidenav-icon.shadow-green {
  filter: drop-shadow(4px 4px 0px var(--pal-green));
}

.questionsbutton {
  height: 120px; /* Full-height: remove this if you want "auto" height */
  width: 40%; /* Set the width of the sidebar */
  z-index: 1; /* Stay on top */
  background-color: var(--pal-red);

  padding: 8px 12px 12px 8px;

  margin-top: 15px;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 10px;

  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;

  float: left;

  filter: var(--neutral-shadow);
}

.content-card {
  padding: 20px;
  margin: 15px;
  background: white;
  border-radius: 10px;
  filter: var(--neutral-shadow);

  height: 90vh;
  display: flex;
  flex-direction: column;

  font-size: large;
  color: black;
}

.content-card-body {

}

.content-card-header {
  font-size: xx-large;
  font-family: 'Lexend', sans-serif;
  margin-bottom: 3vh;
}

.content-card-footer {
  margin-top: auto;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.accordion-button::after {
  background-image: url("images/icons/caret.svg");
  height: 15px;
}

.accordion-button:not(.collapsed)::after {
  background-image: url("images/icons/caret.svg");
  height: 15px;
}

/* width */
::-webkit-scrollbar {
  width: 18px;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--pal-main);
  /*box-shadow: inset 2px 0px 2px rgba(40, 44, 52, 0.2);*/
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--pal-cream);
  border-radius: 10px;
  border-width: 0;

  box-shadow: inset -1px 0px 4px 2px var(--pal-main),
  inset 0 0 0 4.7px /*rgba(40, 44, 52, 1.0)*/ rgb(25, 158, 147);
}
::-webkit-scrollbar-thumb:hover {
  background: #FFCB77; 
}